"use client"
import { IcusRegistration } from "@/lib/slice/auth/cus.auth.types"
import { ChangeEvent, FormEvent, useState } from "react"
import { ToastContainer } from "react-toastify"
import { Eye, EyeOff } from 'lucide-react'
import Link from 'next/link'



//   username: string, email: string,
//   address: {
//     street: string,
//     city: string,
//     ward: string,
//     landmark: string,
//   },
//   phone_number: string;
//   password?: string
// }



const Register = () => {

  const [showPassword, setShowPassword] = useState(false)

  const [regData, setRegData] = useState<IcusRegistration>({
    username: "",
    email: "",
    password: "",
    phone_number: "",

    address: {
      street: "",
      city: "",
      ward: "",
      landmark: "",
    },
  })



  const onChangeData = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target

    if (["street", "city", "ward", "landmark"].includes(name)) {
      setRegData((prev) => ({
        ...prev,
        address: {
          ...prev.address,
          [name]: value,
        },
      }));
    } else {
      setRegData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }


  }


  const onSubmitData = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()

  }
  return <div className="w-full h-[100vh] bg-[url('/register.jpg')]  flex bg-cover bg-no-repeat bg-center bg-fixed overflow-hidden">

    <div className="container flex flex-col relative">

      <form onSubmit={onSubmitData} className="absolute top-20 left-30 mt-5 ">

        <div className=" w-[900px] h-[550px]  my-40 rounded-lg shadow-2xl  shadow-gray-50 border border-gray-200 bg-transparent ">

          <h2 className="text-4xl font-[600] flex justify-center items-center text-black mt-[20px]">Sign Up</h2>
          <div className="flex justify-baseline items-baseline ml-4 mt-5">
            <input onChange={onChangeData} type="text" name="username" placeholder="Username" className="mt-8 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-[300px] p-[4px] text-center text-2xl " required value={regData.username} />

            <input type="email" onChange={onChangeData} name="email" placeholder="Email" className="ml-3 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-full p-[4px] text-center text-2xl mr-2" required value={regData.email} />

          </div>

          <div className="flex justify-baseline items-baseline ml-4">
            <div>

              <input type={`${showPassword ? 'text' : 'password'}`} name="password" onChange={onChangeData} placeholder="Password" className="mt-6 text-gray-800 h-[40px] outline-0 decoration-0 border border-yellow-600  rounded-2xl w-[500px] p-[4px] text-center text-2xl  focus:outline-0 focus:ring-blue-300 " required value={regData.password} /><button onClick={() => setShowPassword(!showPassword)}>{showPassword ? <Eye className="ml-116 mt-[-50] " /> : <EyeOff className="ml-116 mt-[-50]" />}</button>           </div>


            <input type="tel" onChange={onChangeData} name="phone_number" placeholder="Phone Number" className="ml-3 h-[40px]  text-gray-800 outline-0 decoration-0 border border-yellow-600  rounded-2xl w-full p-[4px] text-center text-2xl mr-2" required />

          </div>

          <div className="flex justify-baseline items-baseline ml-4 mt-[-30]">
            <input type="text" onChange={onChangeData} name='street' placeholder="Street" className="mt-8 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-[300px] p-[4px] text-center text-2xl " required value={regData.address.street} />

            <input type="text" onChange={onChangeData} name="city" placeholder="City" className="ml-3 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-full p-[4px] text-center text-2xl mr-2" required value={regData.address.city} />

          </div>

          <div className="flex justify-baseline items-baseline ml-4 mt-[-10]">
            <input type="text" onChange={onChangeData} name="ward" placeholder="Ward" className="mt-8 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-[300px] p-[4px] text-center text-2xl " required value={regData.address.ward} />

            <input type="text" onChange={onChangeData} name="landmark" placeholder="Landmark" className="ml-3 h-[40px] outline-0 decoration-0 border border-yellow-600 text-gray-800  rounded-2xl w-full p-[4px] text-center text-2xl mr-2" required value={regData.address.landmark} />

          </div>

          <button className="w-full h-[60px] border mt-8 text-2xl font-[500] bg-black text-white border-white shadow-2xl shadow-gray-400 rounded-full">Register</button>
          <div className="flex justify-center items-center mt-4 mr-6">          <label className=""><span className="mr-1 ">Have an account?</span ><Link href={'/login'}><p className="inline text-xl font-[500] text-blue-500 hover:text-black cursor-pointer">Login</p></Link></label>
          </div>






        </div>



      </form>


    </div >


  </div >




}

export default Register
